import uniCollapse from '@/colorui/components/uni-collapse.vue'
import uniCollapseItem from '@/colorui/components/uni-collapse-item.vue'
import misEnum from '../../common/mis-enum.js';
export default {
	components: {
		uniCollapse,
		uniCollapseItem
	},
	data() {
		return {
			StatusEnumMap: [],
			steps: [],
			color: '',
			info: {},
			id: "",
			order_code: "",
			applyInfo: {}
		}
	},
	onLoad(option) {
		this.id = option.id;
		this.order_code = option.order_code;
	},
	onShow(){
		//获取派车单信息
		global.$http.post('/car/dispatch/dispatchInfo', {
			params: {
				dispatch_id: this.id
			},
		}).then(res => {
			if (res.status === "0") {
				this.info = res.data;
				this.StatusEnumMap = misEnum.DispatchRecordEnumMap;
				this.showSteps();
			} else {
				uni.showToast({
					title: res.msg,
					icon: 'none'
				});
			}
		}).catch(err => {
			uni.showToast({
				title: err.message,
				icon: 'none'
			});
		});
		//获取申请单信息
		global.$http.post('/car/apply/getInfoByOrderCode', {
			params: {
				order_code: this.order_code
			},
		}).then(res => {
			if (res.status === "0") {
				this.applyInfo = res.data;
			} else {
				uni.showToast({
					title: res.msg,
					icon: 'none'
				});
			}
		}).catch(err => {
			uni.showToast({
				title: err.message,
				icon: 'none'
			});
		});
	},
	methods: {
		showSteps() {
			//设置进度步骤
			this.steps.length = 0;
			misEnum.DispatchRecordEnumMap.forEach((value, key, map) => {
				if (key <= this.info.status) {
					this.steps.push({
						name: this.StatusEnumMap.get(key),
						color: 'text-green'
					});
				} else {
					this.steps.push({
						name: this.StatusEnumMap.get(key),
						color: ''
					});
				}
			});
		},
		getTitle: function(totalFee) {
			return "总费用(元)：" + totalFee;
		},
		sureBack: function(e) {
			uni.navigateTo({
				url: '../work/settleDispatchCar?para=' + encodeURIComponent(JSON.stringify(this.info))
			});
		}
	}
}
